面“唰啦”一声响，周围树起八根大柱，从四面把小船紧紧的箍在中间。三虎伸手一摸，吓得“妈呀”一声惊叫，钻到舱底躲起来了。原来那大柱子是蚆蛸腿，腿上的肉碗子（吸盘）像个小饭盆，弟兄俩没有了主意，就躲在舱底听动静。停了一个时辰，雾散了，天晴了，弟兄爬起来一看，蚆蛸腿不见了。小船已经漂到了岸边。打那以后，这六兄弟再也不敢打鱼挖蚬子了。好端端的一个蛤子湾，被蚆蛸精独占了。半年以后，海湾里的花蛤吃光了，这精灵又向岛上伸爪儿了。一天，二虎在山上耕地，歇气儿的时候把牛放到山坡上吃草。忽听牛直叫，抬头一看，只见海里伸出一条又粗又长的蚆蛸腿，那腿肉乎乎、紫薇薇，在半空中打个旋，朝着半山坡打个旋，朝着半身坡上甩过去，把一头九百多斤的大牛卷起来，眼睁睁的拖到海里就不见了。")]),_vm._v(" "),_c('p',[_vm._v("\n                    当天夜里，海上起了大雾，蛤子湾里传来阵阵哭声，细听起来，象个女人的声音。听着这哭声，弟兄六个无精打采，一个个闷坐着，不知道往后该怎么过。天刚扑亮儿，弟兄六个都做了同样一个梦，梦见一个仙女般的姑娘，泪流满面地来到窗前说：“张家哥哥，快来救我，我是东海龙宫的蛤仙女，为了逃婚，来到这里安家。一心修炼，我知半路上来了个蚆蛸精，把南大湾的蛤子吃了个精光，又把你家的更牛拖下水吃了，看俺长的好看，天天缠着俺，要俺做它的压寨夫人，定于明天正晌午在蛤子湾拜堂成亲。俺如果不从，当场就要把俺杀死几天，然后把俺的皮，吃俺的肉。俺口头应承了，决计明天正晌午时，乘他不备，拼个你死我活。你六兄弟若肯帮我一把，这是就成了。求大哥一定帮忙，你的恩情永世不忘........\n                ")])]),_vm._v(" "),_c('el-col',{staticClass:"right-title",attrs:{"span":7}},[_c('p',[_vm._v("历史与传说")])])],1)],1)],1)}
